import * as THREE from 'three';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import {SpeechBubble} from './SpeechBubble';
import { MainCharacter } from './Character';
import {KLSpace} from './KLSpace';
import {GameControl} from "../GameControl/GameControl";

const PUBLIC_URL = process.env.PUBLIC_URL;


import React, { useRef, useEffect, useState, useCallback } from 'react';


export const KholandSpace = props => {

  const metaRef = useRef(null)
  const [message, setMessage] = useState("");
  const [teleporting, setTeleport] = useState(false);

  // const [_APP, setApp]
  let _APP:KLSpace;
  let timer

  const onIntendCancel=()=>{
    _APP.IntendCancel();
  } 

  // Type:
  // 1: showIntend
  // 2: hideIntend
  // 3: showTeleport
  // 4: dismissTeleport
  const handleIntent = (messageId, messageData) => {
    console.log(messageId, messageData);

    switch(messageId){
      case 1:
        setMessage(`Do you want to play chess with ${messageData.receiver}?`);
        setTeleport(false);
        break;
      case 2:
        setMessage(null);
        setTeleport(false);
        break;
      case 3:
        setMessage(`Teleporting you to chess with ${messageData.receiver}`);
        setTeleport(true);
        timer = setTimeout(teleportTimerCallback, 5000);
        break;
      case 4:
        setMessage(null);
        setTeleport(false);
        clearTimeout(timer);
        break;
    }
  }

  const teleportTimerCallback = useCallback(() => {
    console.log("teleporting");
    //@ts-ignore
    Android.travelToGame()
  }, [])

  const onGameControlMove = (moves)=>{
    console.log("APP MOVE", _APP)
    _APP.move(moves);
  }

  const onGameControlAction = useCallback((action)=>{
    /* 1-yes, 2-no, 3-teleport-cancel */

    if(action==1){
      // setMessage("Teleporing you to game");
      _APP.IntendYes();
    }
    else if(action == 2){
      setMessage(null);
      setTeleport(false)
    }
    else if(action == 3){
      setMessage(null);
      setTeleport(false)
      clearTimeout(timer);
      _APP.IntendCancel();
    }
  }, [])

  useEffect(() => {  
    const div = metaRef.current
    _APP = new KLSpace(div, handleIntent);

    let animationFrameId
    //Our draw came here
    const render = () => {
      animationFrameId = window.requestAnimationFrame(render)
    }
    render()

    return () => {
      window.cancelAnimationFrame(animationFrameId)
    }
  }, []);
  
  return (<div>
    <div 
      style={{
        position: 'relative',
        top:0,
        left:0,
        width:'100%',
        height:'100%',
        overflow:'hidden'
      }}
      ref={metaRef} />
   
    <GameControl
      message={message}
      teleporting={teleporting}
      onMove = {onGameControlMove}
      onAction = {onGameControlAction}
    />
  </div>);
  
}