
import React, { useRef, useEffect, useState } from 'react'
import nipplejs from 'nipplejs';

import './GameControl.css';

export const GameControl = props => {
  
  const divRef = useRef(null)
  const joyStickRef = useRef(null)

  let joystick;

  const handleJoystickEnd = (evt, data) => {
    const keys = {
      forward: false,
      backward: false,
      left: false,
      right: false,
      space: false,
      shift: false,
    };
    props.onMove(keys);
  }

  const handleJoystickMove = (evt, data) => {
    const {x,y} = data.vector;
    const keys = {
      forward: false,
      backward: false,
      left: false,
      right: false,
      space: false,
      shift: false,
    };
    if(y>0.7){
      keys.forward=true;
    }else if(y < -0.7){
      keys.backward=true;
    }
    if(x>0.7){
      keys.right=true;
    }else if(x < -0.7){
      keys.left=true;
    }
    props.onMove(keys);
  }

  const handleYesAction = () => {
    props.onAction(1);
  }

  const handleNoAction = () => {
    props.onAction(2);
  }

  const handleCancelTeleportAction = () => {
    props.onAction(3);
  }

  useEffect(() => {  

    if(joyStickRef){
      joystick = nipplejs.create({
        zone: joyStickRef.current,
        color: '#000',
        size: 150,
        mode: "static",
        position: { bottom: "125px", right: "120px" }
      })[0];

      joystick.on('end', handleJoystickEnd);
      joystick.on('move', handleJoystickMove);
    }

    return () => {
      //Destroy joystick
      if(joystick){
        console.log("joystick", joystick);
        joystick.destroy();
      }
    }
  }, [])
  

  return (
    <div >
      <div 
        ref={joyStickRef} 
      />
       <div className="wrapper" ref={divRef} >
        <div className='controls'>
          <a id="changeStatus">Set Wassup.</a>
        </div>
        <div className="intend" >
          {props.message && !props.teleporting && (<> 
            <div className="dialog">{props.message}</div>
            <a id="yesBtn" className="yesButton" onClick={handleYesAction}>YES!</a>
            <a id="noBtn" className="noButton" onClick={handleNoAction}>X</a>
          </>)}
          {props.message && props.teleporting && (<> 
            <div className="dialog">{props.message}</div>
            <a id="cancelBtn" className="noButton" onClick={handleCancelTeleportAction}>Cancel</a>
          </>
        )}
        </div>
      </div>

    </div>)
}

export default GameControl